import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const state = {
    listaPedidos: []
};

const getters = {
    getListaPedidos(state) {
        return state.listaPedidos;
    }
};

const actions = {
    addListaPedidos(context, item) {
        context.commit('addListaPedidos', item);
    },
    removeListaPedidos(context, index) {
        context.commit('removeListaPedidos', index);
    }
};

const mutations = {
    addListaPedidos(state, item) {
        state.listaPedidos.unshift(item);
    },
    removeListaPedidos(state, index) {
        state.listaPedidos.splice(index, 1);
    }
};



export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
});


